      rateValue: null,
      showlgpicflag: false,
      actflag: false,
      smallbarflag: false
    };
  },
  methods: {
    // getAllHrefs() {
    //   let path = `./static/picturehref.json`;
    //   fs.readFile(path, (err, data) => {
    //     if (err) {
    //       return console.error(err);
    //     } else {
    //       this.allHrefs = JSON.parse(data.toString());
    //       // console.log(this.allHrefs)
    //     }
    //   });
    // },
    showthisFolder(e) {
      this.folderflag = true;
      this.laycont = 4;
      this.hrefs = e;
      this.actflag = true;
      // console.log(this.$refs.allimgdom)
    },
    showlgpic(e) {
      this.showlgpicflag = true;
      this.lghref = e;
    },
    closethis() {
      this.showlgpicflag = false;
    },
    mousemovefunc(e) {
      // console.log(e.pageX, e.pageY,e.screenX,e.screenY)
      let smbarH = this.$refs.smallbar.offsetHeight;
      // console.log(smbarH)
      if (e.pageY / smbarH > 0.8) {
        this.smallbarflag = true;
      } else {
        this.smallbarflag = false;
      }
    },
    delpicture(i) {
    this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.allHrefs.splice(i, 1)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });

      
    }
  },
  created() {
    // this.getAllHrefs();
  }
};
</script>

